<template>
  <div class="badmin-login">
    <div class="login-content">
      <div class="login-title">
        <dl>
          <dt></dt>
          <dd>()</dd>
        </dl>
      </div>
      <div class="login-form">
        <a-form-model ref="loginForm"
                      :model="form"
                      :rules="rules">
          <a-form-model-item prop="username">
            <a-input v-model="form.username"
                     placeholder=""
                     size="large">
              <a-icon slot="prefix"
                      type="user" />
            </a-input>
          </a-form-model-item>
          <a-form-model-item prop="password">
            <a-input-password v-model="form.password"
                              placeholder=""
                              size="large"
                              autocomplete="new-password">
              <a-icon slot="prefix"
                      type="lock" />
            </a-input-password>
          </a-form-model-item>
          <a-form-model-item>
            <a-button type="primary"
                      @click="onSubmit"
                      :loading="loading"
                      size="large"> </a-button>
          </a-form-model-item>
        </a-form-model>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'BadminLogin',
  components: {

  },
  data () {
    let checkNonSpecial = (rule, value, callback) => {
      let reg = /^[0-9A-Za-z]*$/
      if (reg.test(value)) {
        callback()
      } else {
        callback(new Error(''))
      }
    }
    return {
      loginParams: this.$route.query,
      loading: false,
      form: {
        username: '',
        password: ''
      },
      rules: {
        username: [
          { required: true, message: '', trigger: 'blur' },
          { max: 20, message: '20', trigger: 'blur' },
          { validator: checkNonSpecial, trigger: 'blur' }
        ],
        password: [
          { required: true, message: '', trigger: 'blur' },
          { min: 6, max: 20, message: '6-20', trigger: 'blur' },
          { validator: checkNonSpecial, trigger: 'blur' }
        ]
      }
    }
  },
  mounted () {
  },
  methods: {
    onSubmit () {
      let _this = this
      _this.$refs.loginForm.validate(valid => {
        if (valid) {
          _this.loading = true
          _this.login()
        } else {
          _this.$message.error('')
          return false
        }
      })
    },
    async login () {
      const res = await this.$http.post('login', this.form)
      this.loading = false
      if (res && res.data) {
        this.$storage.set('token', res.data)
        this.$message.success('')
        this.goNext()
      }
    },
    goNext () {
      const nextPath = this.loginParams.redirect || '/'
      this.$router.push(nextPath)
      this.loading = false
    }
  }
}
</script>
